import {
  AnimationEntryAst, AnimationQueryAst, AnimationAstVisitor, AnimationStateDeclarationAst,
  AnimationStateTransitionAst, AnimationStepAst, AnimationSequenceAst, AnimationGroupAst, AnimationKeyframeAst,
  AnimationStylesAst
} from "./animation_ast";

export class AnimationSummary {
  constructor(public entries: AnimationEntrySummary[]) {}
  
  get queries(): AnimationQueryAst[] {
    var flatQueries: AnimationQueryAst[] = [];
    this.entries.forEach(entry => {
      flatQueries.push(...entry.queries);
    });
    return flatQueries;
  }
  
  get triggers(): AnimationEntryAst[] {
    return this.entries.map(entry => entry.ast);
  }
}

export class AnimationEntrySummary {
  constructor(public ast: AnimationEntryAst, public queries: AnimationQueryAst[]) {}
}

export function generateAnimationSummary(asts: AnimationEntryAst[]): AnimationSummary {
  var visitor = new AnimationSummaryVisitor();
  return visitor.build(asts);
}

class AnimationSummaryVisitor implements AnimationAstVisitor {
  build(asts: AnimationEntryAst[]): AnimationSummary {
    var summary = new AnimationSummary([]);
    asts.forEach(ast => ast.visit(this, summary));
    return summary;
  }

  visitAnimationEntry(ast: AnimationEntryAst, summary: AnimationSummary): any {
    var triggerSummary = new AnimationEntrySummary(ast, []);
    summary.entries.push(triggerSummary);
    ast.stateDeclarations.forEach(innerAst => innerAst.visit(this, triggerSummary));
    ast.stateTransitions.forEach(innerAst => innerAst.visit(this, triggerSummary));
  }

  visitAnimationStateTransition(ast: AnimationStateTransitionAst, summary: AnimationEntrySummary): any {
    ast.animation.visit(this, summary);
  }

  visitAnimationSequence(ast: AnimationSequenceAst, summary: AnimationEntrySummary): any {
    ast.steps.forEach(innerAst => innerAst.visit(this, summary));
  }

  visitAnimationGroup(ast: AnimationGroupAst, summary: AnimationEntrySummary): any {
    ast.steps.forEach(innerAst => innerAst.visit(this, summary));
  }

  visitAnimationStateDeclaration(ast: AnimationStateDeclarationAst, summary: AnimationEntrySummary): any {}
  visitAnimationKeyframe(ast: AnimationKeyframeAst, summary: AnimationEntrySummary): any {}
  visitAnimationStyles(ast: AnimationStylesAst, summary: AnimationEntrySummary): any {}
  visitAnimationStep(ast: AnimationStepAst, summary: AnimationEntrySummary): any {}

  visitAnimationQuery(ast: AnimationQueryAst, summary: AnimationEntrySummary): any {
    summary.queries.push(ast);
    ast.animation.visit(this, summary);
  }
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
